import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data () {
		return {
			title: '',
			boxShow: 1, // 登录方式box显示
			vPhone: '', // 验证码登录手机号
			vCode: '', // 验证码
			userName: '', // 用户名
			password: '', //密码
			isAuthorize: false,  // 是否第三方授权
		}
	},
	computed:{
		// 是否具备获取验证码的条件
		isverifitionOk() {
			if(this.vPhone) return true
			return false
		},
		// 是否具备提交的条件
		isSubmit(){
			if(this.vPhone && this.vCode || this.userName && this.password) return true
			return false
		}
	},
	onLoad(){
	console.log('我是window',window)	
	},
	methods: {
		// 切换登录方式
		changefun(index) {
			this.boxShow = index
			// 切换时置空所有
			this.vPhone = '';
			this.vCode = '',
			this.userName = '';
			this.password = ''
		},
		// 返回上级目录
		goBack(){
			uni.switchTab({
				url: '../mine/mine'
			})
		},
		// 跳转注册页面
		goregister(){
			uni.navigateTo({
				url: '../register/register'
			})
		},
		// 去除空格
		removespace(str){
			if(typeof str != 'string') return
			return str.replace(/\s+/g, '')
		},
		// 微信登陆
		wxButton(){
			console.log('微信登陆')
			let _this = this
			this.getWxUserInfo()
		},
		// qq登陆
		qqButton(){
			uni.showModal({
				title: '提示',
				content: '敬请期待'
			})
		},
		// 登录
		signin(){
			let _this = this
			let phone = this.removespace(this.vPhone);
			let code = this.removespace(this.vCode);
			let username = this.removespace(this.userName);
			let password = this.removespace(this.password);
			console.log('登陆方式', username,password)
			if(this.boxShow == 2) { // 账号密码登陆
				uni.showLoading({
					title: '登陆中...'
				})
				uni.request({
					url: this.$http + '/api/user/login',
					method: 'Get',
					data: {
						account: username,
						password: password
					},
					success(res){
						console.log('请求成功', res)
						if(res.data.code == 1){
							uni.hideLoading()
							_this.setStorage(res)
						}
					},
					fail(res){
						console.log(res)
					}
				})
			}
			if(this.boxShow == 1){ // 验证码登陆 
				// 记得添加isSigin：true 登陆状态
				uni.showLoading({
					title: '登陆中...'
				})
				uni.request({
					url: this.$http + '/api/user/mobilelogin',
					method: 'POST',
					data: {
						mobile: phone,
						captcha: code
					},
					success: res => {
						uni.hideLoading()
						console.log(res)
						if(res.data.code == 1){
							_this.setStorage(res)
						}
					},
					fail: () => {},
					complete: () => {}
				});
				
			}
			
		},
		// 获取验证码
		getverifition(){
			let phone = this.removespace(this.vPhone);
			if(!this.vPhone){
				uni.showModal({
					content: '请检查手机号是否正确'
				})
				return
			}
			uni.showLoading({
				title: '正在获取验证码'
			})
			uni.request({
				url: this.$http + '/api/sms/sendSms',
				method: 'POST',
				data: {
					mobile: phone,
					event: 'mobilelogin'
				},
				success(res) {
					uni.hideLoading()
					console.log('请求的数据', res)
					if(res.data.code == 0){
						uni.showModal({
							title: '提示',
							content: res.data.msg
						})
					}
				}
			})
		},
		// 获取微信用户信息
		getWxUserInfo(){
			let _this = this
			_this.isAuthorize = true
			wx.showModal({
				title: '操作提示',
				content: '您正在获取用户信息，是否继续？',
				success(res) {
					if(res.confirm){
						// 微信登陆
						uni.showLoading({
							title: '正在授权请稍后...'
						})
						wx.login({
							success(res){
								
								if (res.code){
									let code = res.code
									wx.getUserInfo({
										success(data){
											console.log('微信返回的数据1', data)
											












































											
											
											
											// 小程序端

											console.log('微信端')
												wx.login({
													success:res =>{
														let resData = {
															encryptedData: data.encryptedData,
															iv: data.iv,
															code: res.code
														}
														console.log('微信login返回数据2', res)
														console.log('传递的参数', resData)
														uni.request({
															url: _this.$http + '/api/user/thirdRegis',
															method: 'POST',
															data: resData,
															success(res) {
																console.log('xxxxx',res)
																// 账号验证通过
																if(res.data.code == 1){
																	
																	
																	let newRes = res
																	let datas = newRes.data.data.userinfo
																	datas.type = 1 // 三方授权登陆
																	datas.isSigin = true
																	_this.$store.commit('setUserInfo', datas)
																	console.log('成功获取为微信信息', _this.$store.state.userInfo)
																	// 登陆状态保持
																	console.log("newRes",newRes)
																	_this.setStorage(newRes)
																	uni.switchTab({
																		url: '../mine/mine'
																	})
																	
																	
																}else if(res.data.code == 2){ // 用户第三方授权，但未绑定手机号
																	uni.showModal({
																		title: '提示',
																		content: res.data.msg,
																		success(res){
																			if(res.confirm){
																				uni.navigateTo({
																					url: '../register/register'
																				})
																			}
																		}
																	})
																	
																}else{
																	uni.showModal({
																		title: '提示',
																		content: res.data.msg
																	})
																}
																uni.hideLoading()
															}
														})
													}
												})
											

											
											
											
											
										},
										fail: res => {
											uni.showModal({
												title: '提示',
												content: 'Error' 
											})
										}
									})
								}
							}
						})
					}else{
							console.log('您已取消')
					}
				}
			})
		},
		// 保持登陆状态
		setStorage(res){
			let resData = {
				token: res.data.data.userinfo.token,
				nickname: res.data.data.userinfo.nickname,
				username: res.data.data.userinfo.username,
				avatar: res.data.data.userinfo.avatar,
				score: res.data.data.userinfo.score,
				sex: res.data.data.userinfo.sex,
				mobile: res.data.data.userinfo.mobile,
				user_id: res.data.data.userinfo.user_id,
				type: 2,
				isSigin: true
			}
			this.$store.commit('setUserInfo', resData)
			// 保持登陆状态
			console.log('widnow', window)
			uni.setStorage({
				key: 'srorage_token',
				data: resData.token
			})
			uni.setStorage({
				key: 'srorage_isSigin',
				data: resData.isSigin
			})
			uni.setStorage({
				key: 'srorage_nickname',
				data: resData.nickname
			})
			uni.setStorage({
				key: 'srorage_username',
				data: resData.username
			})
			uni.setStorage({
				key: 'srorage_avatar',
				data: resData.avatar
			})
			uni.setStorage({
				key: 'srorage_score',
				data: resData.score
			})
			uni.setStorage({
				key: 'srorage_sex',
				data: resData.sex
			})
			uni.setStorage({
				key: 'srorage_mobile',
				data: resData.mobile
			})
			uni.setStorage({
				key: 'srorage_type',
				data: resData.type
			})
			uni.setStorage({
				key: 'srorage_user_id',
				data: resData.user_id
			})
			
			uni.switchTab({
				url: '../mine/mine'
			})
		}
	},
	components:{
		Topbar
	}
}
